<template>
  <div class="flex-box is-vertical">
    <div class="toolbar">
      <el-popover width="200" title="" trigger="hover">
        <div style="display: flex; flex-direction: column">
          <!--  -->
          <el-input v-model="currentChartTitle" placeholder="" @change="initChart"></el-input>
          <el-input v-model="currentChartSubTitle" placeholder="" @change="initChart"></el-input>
          <div>
            <el-radio-group v-model="ctype" @change="typeChange">
              <el-row>
                <el-col :span="12" v-for="(item, index) in supportedArr" :key="index">
                  <el-radio :style="'margin-top: 10px'" :label="item.value"> {{ item.type }} </el-radio>
                </el-col>
              </el-row>
            </el-radio-group>
          </div>
          <!-- <div :style="popoverDivStyle">
            <el-row>
              <el-col :span="12">
                <span></span>
              </el-col>
              <el-col :span="12">
                <el-switch v-model="titlevis" @change="onChangeTitle"></el-switch>
              </el-col>
            </el-row>
          </div>
          <div :style="popoverDivStyle">
            <el-row>
              <el-col :span="12">
                <span></span>
              </el-col>
              <el-col :span="12">
                <el-switch v-model="legendvis" @change="onChangeLegend"></el-switch>
              </el-col>
            </el-row>
          </div> -->
          <div :style="popoverDivStyle">
            <el-row>
              <el-col :span="12">
                <span></span>
              </el-col>
              <el-col :span="12">
                <el-switch v-model="zoomvisible" @change="onChangeZoom"></el-switch>
              </el-col>
            </el-row>
          </div>
        </div>
        <div v-if="rawdata != ''">
          <el-radio-group v-model="groupId" @change="groupChange">
            <el-row>
              <el-col :span="12" v-for="(item, index) in columnDict" :key="index">
                <el-radio :label="index"> {{ item }} </el-radio>
              </el-col>
            </el-row>
          </el-radio-group>
        </div>
        <div v-if="ctype == 'pie' && dataColumns > 1">
          <h4></h4>
          <el-slider v-model="col" :min="1" :max="dataColumns" @change="onAfterChange"></el-slider>
        </div>
        <el-button slot="reference" icon="el-icon-setting" size="mini" circle></el-button>
      </el-popover>
    </div>
    <div ref="chart" :style="chartStyle"></div>
    <!-- <div v-if="rawdata" style="width: 100%; height: 20px;"></div>
    <div v-if="!rawdata" style="width: 100%; height: 20px;"></div> -->
  </div>
</template>
<script>
import echarts from 'echarts'
import { loadSchema, loadSupportedType } from './chart'
import chartDef from '../chartDef.js'

export default {
  extends: chartDef,
  name: 'v-chart',
  props: {
    width: {
      default: '100%'
    },
    height: {
      default: '100%'
    },
    //echarts dataset
    dataSource: {
      type: Array,
      default: () => []
    },
    color: {
      type: Array,
      //echarts
      default: () => [
        '#c23531',
        '#2f4554',
        '#61a0a8',
        '#d48265',
        '#91c7ae',
        '#749f83',
        '#ca8622',
        '#bda29a',
        '#6e7074',
        '#546570',
        '#c4ccd3'
      ]
    },
    type: {
      type: String,
      default: 'bar'
    },
    chartTitle: {
      type: String,
      default: ''
    },
    chartSubTitle: {
      type: String,
      default: ''
    },
    showAlllabel: {
      type: Boolean,
      default: false
    },
    zoomvis: {
      type: Boolean,
      default: false
    },
    options: {
      type: Object,
      default: () => ({
        options: 'noOption'
      })
    },
    tooltip: {
      type: Object,
      default: () => ({
        options: 'noOption'
      })
    },
    legendvis: {
      type: Boolean,
      default: true
    },
    titlevis: {
      type: Boolean,
      default: true
    },
    rawdata: {
      default: ''
    }
  },
  data() {
    return {
      //
      col: 2,
      //echart
      currentChart: null,
      //
      ctype: 'bar',
      //
      schema: null,
      //
      zoomvisible: this.zoomvis,
      //
      supportedArr: null,
      //
      processedArr: null,
      //
      columnDict: [],
      //
      currentChartTitle: '',
      //
      currentChartSubTitle: '',
      radio: 1,
      groupId: 0,
      popoverDivStyle: {
        'margin-top': '10px'
      }
    }
  },
  watch: {
    dataSource: function(newVal, oldVal) {
      if (JSON.stringify(newVal) != JSON.stringify(oldVal)) {
        this.updateChart()
      }
    }
  },
  computed: {
    legendvisible: function() {
      return this.legendvis
    },
    //
    titlevisible: function() {
      return this.titlevis
    },
    //
    //(series)
    dataColumns: function() {
      if (this.dataSource.length == 0) {
        return 1
      } else {
        return this.dataSource[0].length - 1
      }
    },
    //,
    chartStyle: function() {
      return {
        width: this.width,
        height: this.height
      }
    },
    //,,
    series: function() {
      if (this.rawdata == '') {
        let col = this.col //
        let row = Math.ceil(this.dataColumns / col) //
        let gapRateX = 100 / col //x
        let gapRateY = 100 / row //y
        let r = Math.min((100 / col) * 0.7, (100 / row) * 0.7) + '%' //*0.7
        // let chartType = this.type //
        let chartType = this.ctype
        let offsetX = 100 / col / 2 //x
        let offsetY = 100 / row / 2 //y
        // let count = this.dataColumns //
        let result = [] //series
        if (chartType == 'pie') {
          for (let i = 0; i < this.dataColumns; i++) {
            result[i] = {
              type: chartType,
              radius: r,
              // center: [200 + (i % col) * 4 * r, 250 + Math.floor(i / 2) * 3 * r],
              center: [offsetX + (i % col) * gapRateX + '%', offsetY + Math.floor(i / col) * gapRateY + '%'],
              encode: { itemName: this.dataSource[0][0], value: this.dataSource[0][i + 1] }
            }
          }
        } else {
          for (let i = 0; i < this.dataColumns; i++) {
            result[i] = { type: chartType }
          }
        }
        return result
      } else {
        return this.rawseries
      }
    },
    //,series,
    rawseries: function() {
      let chartType = this.ctype
      let result = {} //rawseries
      if (chartType == 'pie') {
        result = {
          type: chartType,
          radius: '60%',
          center: ['50%', '50%']
        }
      } else {
        result = { type: chartType }
      }
      return result
    },
    //
    interval: function() {
      if (this.showAlllabel) {
        return { interval: 0 }
      } else {
        return {}
      }
    }
  },
  mounted() {
    this.initCurrentTitle()
    this.initColumnDict()
    this.countArr(4)
    this.getSupportedArr()
    this.initChart()
    window.addEventListener('resize', () => {
      this.currentChart.resize()
    })
  },
  methods: {
    confirmChange(value) {},
    //
    getSupportedArr() {
      this.supportedArr = loadSupportedType()
    },
    //slidebarmouseup
    onAfterChange(val) {
      this.col = val
      this.initChart()
    },
    //
    // onChangeLegend(val) {
    //   this.legendvis = val
    //   this.initChart()
    // },
    // //
    // onChangeTitle(val) {
    //   this.titlevis = val
    //   this.initChart()
    // },
    //
    onChangeZoom(val) {
      this.zoomvisible = val
      this.initChart()
    },
    //
    typeChange(e) {
      this.initChart()
    },
    //
    groupChange(value) {
      this.countArr(value)
      this.initChart()
    },
    //
    initSchema() {
      return loadSchema(this.ctype)
    },
    //
    initTitle(param) {
      return {
        text: this.currentChartTitle,
        subtext: this.currentChartSubTitle,
        left: this.titlepos,
        show: param
      }
    },
    //
    initCurrentTitle() {
      this.currentChartTitle = this.chartTitle
      this.currentChartSubTitle = this.chartSubTitle
    },
    //tooltip
    initTooltip(param) {
      if (param == true) {
        return this.tooltip
      }
    },
    //
    initAxis(param) {
      return param
    },
    //
    initDataZoom(param) {
      let disabled = 100
      let abled = 50
      let dataZoom = [
        {
          type: 'slider',
          xAxisIndex: 0,
          start: 0,
          end: 50,
          show: param
        },
        {
          type: 'slider',
          yAxisIndex: 0,
          start: 0,
          end: 50,
          show: param
        }
      ]
      if (param == true) {
        dataZoom.forEach(element => {
          element.show = param
          element.end = abled
        })
        return dataZoom
      } else {
        dataZoom.forEach(element => {
          element.show = param
          element.end = disabled
        })
        return dataZoom
      }
    },
    //
    initDataSource(param) {
      if (param == true) {
        return { source: this.dataSource }
      }
    },
    //
    initChart() {
      //dataSource[0].length-1
      this.currentChart = echarts.init(this.$refs.chart)
      this.schema = this.initSchema()
      //
      if (this.options.options == 'noOption' && this.rawdata == '') {
        let option = {
          title: this.initTitle(this.titlevisible),
          tooltip: this.initTooltip(this.schema.tooltip),
          legend: {
            bottom: '0',
            show: this.legendvisible
          },
          xAxis: {
            type: 'category',
            //label
            axisLabel: this.interval,
            show: this.initAxis(this.schema.showAxis)
          },
          yAxis: {
            show: this.initAxis(this.schema.showAxis)
          },
          itemStyle: {
            // borderColor: '#fff',
            // borderWidth: 1,
            opacity: this.opacity
          },
          dataZoom: this.initDataZoom(this.zoomvisible),
          //
          dataset: this.initDataSource(this.schema.dataSource),
          color: this.color,
          //
          series: this.series
        }
        this.currentChart.setOption(option)
        //
      } else if (this.options.options == 'noOption' && this.rawdata != '') {
        let option = {
          title: this.initTitle(this.titlevisible),
          tooltip: this.initTooltip(this.schema.tooltip),
          legend: {
            bottom: '0',
            // type: 'scroll',
            show: this.legendvisible
          },
          xAxis: {
            type: 'category',
            axisLabel: this.interval,
            show: this.initAxis(this.schema.showAxis)
          },
          yAxis: {
            show: this.initAxis(this.schema.showAxis)
          },
          itemStyle: {
            // borderColor: '#fff',
            // borderWidth: 1,
            opacity: this.opacity
          },
          dataZoom: this.initDataZoom(this.zoomvisible),
          dataset: {
            source: this.processedArr
          },
          color: this.color,
          series: this.rawseries
        }
        this.currentChart.setOption(option)
        //optionsdatasetoptions
      } else {
        let option = this.options
        this.currentChart.setOption(option)
      }
    },
    //resize
    chartResize() {
      this.currentChart.resize()
    },
    //
    countArr(index) {
      let dataList = []
      //index,index
      for (let i = 0; i < this.rawdata.length; i++) {
        dataList[i] = this.rawdata[i][index]
      }
      //set,key
      let set = new Set()
      for (let i = 1; i < dataList.length; i++) {
        set.add(dataList[i])
      }
      //map,setkey,keyvalue0
      let map = new Map()
      for (let x of set) {
        map.set(x, 0)
      }
      //dataList0,
      let detail = []
      for (let i = 1; i < dataList.length; i++) {
        detail[i - 1] = dataList[i]
      }
      //detail,key,valuekeyvalue + 1
      for (let x of detail) {
        map.set(x, map.get(x) + 1)
      }
      //maparray,0
      const maparr = [[dataList[0], ''], ...map]
      //arrayprocesseArr
      this.processedArr = maparr
    },
    //,key
    initColumnDict() {
      this.columnDict = this.rawdata[0]
    },
    updateChart() {
      this.currentChart.clear()
      this.schema = this.initSchema()
      let option = {
        title: this.initTitle(this.titlevisible),
        tooltip: this.initTooltip(this.schema.tooltip),
        legend: {
          bottom: '0',
          show: this.legendvisible
        },
        xAxis: {
          type: 'category',
          axisLabel: this.interval,
          show: this.initAxis(this.schema.showAxis)
        },
        yAxis: {
          show: this.initAxis(this.schema.showAxis)
        },
        itemStyle: {
          // borderColor: '#fff',
          // borderWidth: 1,
          opacity: this.opacity
        },
        dataZoom: this.initDataZoom(this.zoomvisible),
        dataset: this.initDataSource(this.schema.dataSource),
        color: this.color,
        series: this.series
      }
      this.currentChart.setOption(option)
    }
  }
}
</script>

