import url from '@mock/mockAPI.js'

export default {
  data() {
    return {
      //
      preventActiveBehavior: false,
      //namecompX
      name: '',
      //
      active: false,
      //
      ifshow: false,
      //
      width: 500,
      //
      height: 500,
      //
      top: 100,
      //
      left: 100,
      //
      deg: 0,
      //
      parentLimitation: true,
      //
      draggable: true,
      //
      resizable: true,
      //
      rotatable: true,
      //
      index: 0,
      //z-index
      zindex: 0,
      //
      mode: 'design',
      //
      flag: false,
      //
      title: '',
      //
      subTitle: '',
      ///
      dataSource: {
        data: []
      },
      //
      style: {
        opacity: 1,
        titlepos: ''
        // legendvis: true,
        // titlevis: true
      }
    }
  },
  watch: {},
  computed: {
    //X
    parentScaleX: function() {
      if (this.mode == 'design') {
        return this.$store.state.parentScaleX
      } else {
        return 1
      }
    },
    //Y
    parentScaleY: function() {
      if (this.mode == 'design') {
        return this.$store.state.parentScaleY
      } else {
        return 1
      }
    },
    //
    parentW: function() {
      if (this.mode == 'design') {
        return this.$store.state.screenDef[0].value
      } else {
      }
    },
    //
    parentH: function() {
      if (this.mode == 'design') {
        return this.$store.state.screenDef[1].value
      } else {
      }
    }
  },
  mounted() {
    // console.log(this)
  },
  methods: {
    // 
    updateComponentList() {
      if (this.mode == 'design') {
        this.$axios({
          url: url.adjustComponent,
          method: 'post',
          data: {
            templateID: this.$store.state.currentTemplateID,
            index: this.index,
            width: this.width,
            height: this.height,
            top: this.top,
            left: this.left
          }
        }).then(res => {
          if (res.status == 200) {
            this.$store.commit('initComponentList', res.data.resultSet)
          }
        })
      }
    },
    //mainCanvasobjListmount()
    destroyComponent() {
      this.ifshow = false
      this.$emit('destroyComponent', this.index)
    },
    //
    resize(newRect) {
      this.width = newRect.width
      this.height = newRect.height
      this.top = newRect.top
      this.left = newRect.left
    },
    //activeinactive,
    onDeactivated() {
      this.$refs.testref.blur()
    },
    //inactiveactive,
    activate() {
      this.$emit('updateActiveStatus', this.index)
      this.$refs.testref.focus()
    },
    rotate(deg) {
      // console.log(deg)
      this.style.deg = deg
    }
  }
}
