import url from '@mock/mockAPI.js'

export default {
  data() {
    return {
      //
      preventActiveBehavior: false,
      //namecompX
      name: '',
      //
      active: false,
      //
      ifshow: false,
      //
      width: 500,
      //
      height: 500,
      //
      top: 100,
      //
      left: 100,
      //
      deg: 0,
      //
      parentLimitation: true,
      //
      draggable: true,
      //
      resizable: true,
      //
      rotatable: true,
      //
      index: 0,
      //z-index
      zindex: 0,
      //
      mode: 'design',
      //
      flag: false,
      //
      title: '',
      //
      subTitle: '',
      ///
      dataSource: {
        data: [],
        dataSourceOptions: [
          {
            value: 'APISource',
            label: 'API'
          },
          {
            value: 'otherSource',
            label: 'unknown'
          }
        ],
        dataSourceType: '',
        APISwitch: false,
        APIURL: '',
        APIMethod: '',
        APIInterval: 10000,
        APIHeader: '',
        IntervalID: 0 //setIntervalIDclearInterval
      },
      //
      style: {
        opacity: 1,
        titlepos: 'center'
        // legendvis: true,
        // titlevis: true
      }
    }
  },
  watch: {
    'dataSource.dataSourceType': function(newVal) {
      // console.log(newVal)
      if (newVal == 'APISource') {
        this.dataSource.APISwitch = true
      } else {
        this.dataSource.APISwitch = false
      }
    },
    'dataSource.APIURL': {
      immediate: true,
      handler(newVal) {
        // console.log(newVal)
        clearInterval(this.dataSource.IntervalID)
        this.dataSource.IntervalID = ''
        console.log('APIURL updatedInterval cleared')
        if (this.dataSource.APISwitch == true) {
          // let patt = /http:/
          this.dataSource.IntervalID = setInterval(this.fetchDataFromAPI, this.dataSource.APIInterval)
          console.log('Interval set')
        }
      }
    },
    'dataSource.APIMethod': {
      // immediate: true,
      handler(newVal) {
        clearInterval(this.dataSource.IntervalID)
        this.dataSource.IntervalID = ''
        console.log('APIMethod updatedInterval cleared')
        if (this.dataSource.APISwitch == true && this.dataSource.IntervalID == '') {
          this.dataSource.IntervalID = setInterval(this.fetchDataFromAPI, this.dataSource.APIInterval)
          console.log('Interval set')
        }
      }
    },
    'dataSource.APIInterval': {
      // immediate: true,
      handler(newVal) {
        clearInterval(this.dataSource.IntervalID)
        this.dataSource.IntervalID = ''
        console.log('APIInterval updatedInterval cleared')
        if (this.dataSource.APISwitch == true && this.dataSource.IntervalID == '') {
          this.dataSource.IntervalID = setInterval(this.fetchDataFromAPI, this.dataSource.APIInterval)
          console.log('Interval set')
        }
      }
    },
    'dataSource.APISwitch': function(newVal) {
      // console.log(newVal)
      if (newVal == false) {
        console.log('APISwitch offInterval cleared')
        clearInterval(this.dataSource.IntervalID)
        this.dataSource.IntervalID = ''
      }
    }
  },
  computed: {
    //X
    parentScaleX: function() {
      if (this.mode == 'design') {
        return this.$store.state.parentScaleX
      } else {
        return 1
      }
    },
    //Y
    parentScaleY: function() {
      if (this.mode == 'design') {
        return this.$store.state.parentScaleY
      } else {
        return 1
      }
    },
    //
    parentW: function() {
      if (this.mode == 'design') {
        return this.$store.state.screenDef[0].value
      } else {
      }
    },
    //
    parentH: function() {
      if (this.mode == 'design') {
        return this.$store.state.screenDef[1].value
      } else {
      }
    }
  },
  mounted() {
    // console.log(this)
  },
  methods: {
    //
    updateComponentList() {
      if (this.mode == 'design') {
        // console.log(this.index)
        // console.log(this.$store.state.currentTemplateID)
        this.$axios({
          url: url.adjustComponent,
          method: 'post',
          data: {
            templateID: this.$store.state.currentTemplateID,
            deg: this.deg,
            index: this.index,
            width: this.width,
            height: this.height,
            top: this.top,
            left: this.left
          }
        }).then(res => {
          if (res.status == 200) {
            console.log(res.data.resultSet)
            this.$store.commit('initComponentList', res.data.resultSet)
            this.$store.commit('resizeUpdateActiveComponent')
          }
        })
      }
    },
    //mainCanvasobjListmount()
    destroyComponent() {
      this.ifshow = false
      this.$emit('destroyComponent', this.index)
    },
    chartResize() {
      this.$refs.child.chartResize()
    },
    //
    resize(newRect) {
      this.width = newRect.width
      this.height = newRect.height
      this.top = newRect.top
      this.left = newRect.left
      this.$refs.child.chartResize()
    },
    //activeinactive,
    onDeactivated() {
      this.$refs.testref.blur()
    },
    //inactiveactive,
    activate() {
      this.$emit('updateActiveStatus', this.index)
      this.$refs.testref.focus()
    },
    rotate(deg) {
      // console.log(deg)
      this.deg = deg
    },
    testFetchData() {
      // console.log(this.dataSource.APIURL)
      // console.log(this.dataSource.APIMethod)
      if (this.dataSource.APISwitch) {
        this.$axios({
          url: this.dataSource.APIURL,
          method: this.dataSource.APIMethod,
          data: {}
        }).then(res => {
          console.log(res.data)
          this.dataSource.data = res.data
        })
      }
    },
    fetchDataFromAPI() {
      if (this.dataSource.APISwitch) {
        let currentHeader = {}
        try {
          if (this.dataSource.APIHeader != '') {
            currentHeader = JSON.parse(this.dataSource.APIHeader)
          }
        } catch (error) {
          console.log(error)
        }
        this.$axios({
          url: this.dataSource.APIURL,
          method: this.dataSource.APIMethod,
          headers: currentHeader,
          data: {}
        }).then(res => {
          if (res.status == 200) {
            this.dataSource.data = res.data
            console.log(res.data)
          }
        })
      }
      console.log(this.dataSource.APIInterval)
      console.log(this.dataSource.IntervalID)
    }
  }
}
